"use client"

import React, { useEffect, useRef, useState } from "react"
import { useSearchParams, useRouter } from "next/navigation"

import { Button } from "@/components/shadcn/ui/button"
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/components/shadcn/ui/card"
import { Input } from "@/components/shadcn/ui/input"
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/shadcn/ui/tabs"
import { FilterIcon } from "lucide-react"

import { LibraryCard, type ItemProps } from "./libraryCard"

const dataList: ItemProps[] = Array.from({ length: 1000 }, (_, i) => ({
  id: i,
  title: `Mục ${i}`,
  category: "Thể loại",
  isPublic: i % 2 === 0,
  language: "Tiếng Việt",
  owner: `Người ${i}`,
  updatedAt: new Date().toISOString(),
  isStarred: i % 3 === 0,
}))

export default function Page() {
  const searchParams = useSearchParams()
  const router = useRouter()
  const tabParam = searchParams.get("tab") || "1"

  const handleTabChange = (value: string) => {
    const params = new URLSearchParams(searchParams.toString())
    params.set("tab", value)
    router.replace(`?${params.toString()}`, { scroll: false })
  }

  const [visibleItems, setVisibleItems] = useState<ItemProps[]>([])
  const itemsPerLoad = 12
  const loadMoreRef = useRef<HTMLDivElement | null>(null)
  const loadedCountRef = useRef<number>(0)

  useEffect(() => {
    // Load initial items
    const initialItems = dataList.slice(0, itemsPerLoad)
    setVisibleItems(initialItems)
    loadedCountRef.current = itemsPerLoad
  }, [])

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        const isVisible = entries[0].isIntersecting
        if (isVisible) {
          const next = dataList.slice(
            loadedCountRef.current,
            loadedCountRef.current + itemsPerLoad
          )
          if (next.length > 0) {
            setVisibleItems((prev) => [...prev, ...next])
            loadedCountRef.current += itemsPerLoad
          }
        }
      },
      {
        rootMargin: "0px",
        threshold: 1.0,
      }
    )

    if (loadMoreRef.current) {
      observer.observe(loadMoreRef.current)
    }

    return () => {
      if (loadMoreRef.current) {
        observer.unobserve(loadMoreRef.current)
      }
    }
  }, [])

  return (
    <Tabs value={tabParam} onValueChange={handleTabChange} className="w-full">
      <TabsList className="grid w-[400px] grid-cols-3">
        <TabsTrigger value="1">Thư viện của tôi</TabsTrigger>
        <TabsTrigger value="2">Đã lưu</TabsTrigger>
        <TabsTrigger value="3">Khám phá</TabsTrigger>
      </TabsList>

      <TabsContent value="1">
        <Card>
          <CardHeader>
            <CardTitle>Thư viện của tôi</CardTitle>
            <div className="flex items-center justify-between mt-2">
              <div className="flex items-center space-x-2">
                <Input placeholder="Tìm kiếm..." className="w-[200px]" />
                <Button variant="outline" size="icon">
                  <FilterIcon className="h-4 w-4" />
                </Button>
              </div>
              <Button>Thêm mới</Button>
            </div>
          </CardHeader>

          <CardContent>
            <div className="grid grid-cols-[repeat(auto-fill,_minmax(250px,_1fr))] gap-4">
              {visibleItems.map((item) => (
                <LibraryCard key={item.id} item={item} />
              ))}
            </div>

            <div
              ref={loadMoreRef}
              className="h-10 mt-4 flex justify-center items-center"
            >
              {visibleItems.length < dataList.length ? (
                <span className="text-sm text-muted-foreground">
                  Đang tải thêm...
                </span>
              ) : (
                <span className="text-sm text-muted-foreground">
                  Đã tải hết nội dung
                </span>
              )}
            </div>
          </CardContent>
        </Card>
      </TabsContent>
    </Tabs>
  )
}
